'use client';
import React, { useState } from "react";
import { Card, CardContent } from "@/components/ui/card";
import { Input } from "@/components/ui/input";
import { Separator } from "@/components/ui/separator";
import {
  LineChart,
  Line,
  XAxis,
  YAxis,
  CartesianGrid,
  Tooltip,
  Legend,
  ResponsiveContainer,
} from "recharts";

function downloadCSV(data, filename) {
  const csv = [
    Object.keys(data[0]).join(","),
    ...data.map(row => Object.values(row).join(","))
  ].join("\n");

  const blob = new Blob([csv], { type: "text/csv" });
  const url = URL.createObjectURL(blob);
  const a = document.createElement("a");
  a.href = url;
  a.download = `${filename}.csv`;
  a.click();
  URL.revokeObjectURL(url);
}

export default function InvestmentDashboard() {
  const [rentabilidade, setRentabilidade] = useState(3.55);
  const [investInicial, setInvestInicial] = useState(20000);
  const [aporteMensal, setAporteMensal] = useState(1500);
  const [aporteSazonal, setAporteSazonal] = useState(8000);
  const [mesSazonal, setMesSazonal] = useState(4);
  const [vendasMensais, setVendasMensais] = useState(40);
  const [nMeses, setNMeses] = useState(12);
  const [n1, setN1] = useState(5);
  const [n2, setN2] = useState(10);
  const [n3, setN3] = useState(15);
  const [investInicialIndicado, setInvestInicialIndicado] = useState(20000);
  const [aporteMensalIndicado, setAporteMensalIndicado] = useState(1000);
  const [aporteSazonalIndicado, setAporteSazonalIndicado] = useState(5000);
  const [investirLicencas, setInvestirLicencas] = useState(false);
  const [investirComissoes, setInvestirComissoes] = useState(false);
  const [graficoFinanceiro, setGraficoFinanceiro] = useState(true);
  const [rangeFiltro, setRangeFiltro] = useState([1, 12]);
  const [verFinanceiro, setVerFinanceiro] = useState(true);


  const licencaAfiliado = 277.9;

  const simulate = () => {
    let capital = investInicial;
    let simulacao = [];
    let totalLucro = 0;
    let totalReceitaLicenca = 0;
    let totalComissao = 0;

    let capitalIndicadoN1 = investInicialIndicado;
    let capitalIndicadoN2 = investInicialIndicado;
    let capitalIndicadoN3 = investInicialIndicado;

    for (let mes = 1; mes <= nMeses; mes++) {
      capital += aporteMensal;
      capitalIndicadoN1 += aporteMensalIndicado;
      capitalIndicadoN2 += aporteMensalIndicado;
      capitalIndicadoN3 += aporteMensalIndicado;

      if (mes === mesSazonal) {
        capital += aporteSazonal;
        capitalIndicadoN1 += aporteSazonalIndicado;
        capitalIndicadoN2 += aporteSazonalIndicado;
        capitalIndicadoN3 += aporteSazonalIndicado;
      }

      const lucro = capital * (rentabilidade / 100);
      const lucroIndicadoN1 = capitalIndicadoN1 * (rentabilidade / 100);
      const lucroIndicadoN2 = capitalIndicadoN2 * (rentabilidade / 100);
      const lucroIndicadoN3 = capitalIndicadoN3 * (rentabilidade / 100);

      capital += lucro;
      capitalIndicadoN1 += lucroIndicadoN1;
      capitalIndicadoN2 += lucroIndicadoN2;
      capitalIndicadoN3 += lucroIndicadoN3;

      const receitaLicenca = vendasMensais * licencaAfiliado;
      const comissao =
        lucroIndicadoN1 * n1 * 0.55 +
        lucroIndicadoN2 * n2 * 0.25 +
        lucroIndicadoN3 * n3 * 0.15;

      if (investirLicencas) capital += receitaLicenca;
      if (investirComissoes) capital += comissao;

      totalLucro += lucro;
      totalReceitaLicenca += receitaLicenca;
      totalComissao += comissao;

      simulacao.push({
        mes: `Mês ${mes}`,
        capital: parseFloat(capital.toFixed(2)),
        lucro: parseFloat(lucro.toFixed(2)),
        receitaLicenca: parseFloat(receitaLicenca.toFixed(2)),
        comissao: parseFloat(comissao.toFixed(2)),
        capitalIndicadoN1: parseFloat(capitalIndicadoN1.toFixed(2)),
        capitalIndicadoN2: parseFloat(capitalIndicadoN2.toFixed(2)),
        capitalIndicadoN3: parseFloat(capitalIndicadoN3.toFixed(2)),
      });
    }

    return {
      data: simulacao,
      totalLucro,
      totalReceitaLicenca,
      totalComissao,
      capitalFinal: capital,
    };
  };

  const {
    data,
    totalLucro,
    totalReceitaLicenca,
    totalComissao,
    capitalFinal,
  } = simulate();

  const dataFiltrada = data.filter((_, i) => {
    const mes = i + 1;
    return mes >= rangeFiltro[0] && mes <= rangeFiltro[1];
  });

  return (
    <div className="p-6 space-y-6 bg-[#0c0c0c] text-white min-h-screen font-sans">
      <div className="flex items-center gap-4">
        <img src="/logo.png" alt="Blackhole Capital" className="h-12" />
        <h1 className="text-3xl font-bold text-cyan-400">
          Projeção de Investimentos
        </h1>
      </div>

      <Separator className="bg-cyan-400 my-4" />

      <div className="grid grid-cols-2 md:grid-cols-4 gap-4 text-center">
        <Card className="bg-gradient-to-br from-cyan-500/30 to-transparent border-cyan-400">
          <CardContent className="p-4 text-white">
            <p className="text-sm">Capital Final</p>
            <p className="text-xl font-semibold">
              R$ {capitalFinal.toLocaleString()}
            </p>
          </CardContent>
        </Card>
        <Card className="bg-gradient-to-br from-green-500/30 to-transparent border-green-400">
          <CardContent className="p-4 text-white">
            <p className="text-sm">Lucro Total</p>
            <p className="text-xl font-semibold">
              R$ {totalLucro.toLocaleString()}
            </p>
          </CardContent>
        </Card>
        <Card className="bg-gradient-to-br from-orange-500/30 to-transparent border-orange-400">
          <CardContent className="p-4 text-white">
            <p className="text-sm">Receita com Licenças</p>
            <p className="text-xl font-semibold">
              R$ {totalReceitaLicenca.toLocaleString()}
            </p>
          </CardContent>
        </Card>
        <Card className="bg-gradient-to-br from-red-500/30 to-transparent border-red-400">
          <CardContent className="p-4 text-white">
            <p className="text-sm">Comissões Recebidas</p>
            <p className="text-xl font-semibold">
              R$ {totalComissao.toLocaleString()}
            </p>
          </CardContent>
        </Card>
      </div>

      <div className="flex items-center gap-4 flex-wrap justify-between">
        <div className="flex items-center gap-4">
          <label className="text-sm">Filtrar intervalo de meses:</label>
          <input
            type="range"
            min="1"
            max={nMeses}
            value={rangeFiltro[0]}
            onChange={(e) =>
              setRangeFiltro([+e.target.value, rangeFiltro[1]])
            }
            className="w-24"
          />
          <span className="text-sm">de {rangeFiltro[0]}</span>
          <input
            type="range"
            min="1"
            max={nMeses}
            value={rangeFiltro[1]}
            onChange={(e) =>
              setRangeFiltro([rangeFiltro[0], +e.target.value])
            }
            className="w-24"
          />
          <span className="text-sm">até {rangeFiltro[1]}</span>
        </div>
        <button
          className="bg-cyan-700 hover:bg-cyan-800 text-white px-4 py-2 rounded text-sm font-semibold"
          onClick={() => setVerFinanceiro(!verFinanceiro)}
        >
          Ver {verFinanceiro ? "Evolução da Rede" : "Evolução Financeira"}
        </button>
      </div>

      <Card className="mt-4">
        <CardContent className="p-4">
          <h2 className="text-lg font-bold text-cyan-300 mb-2">
            {verFinanceiro ? "Evolução Financeira" : "Evolução da Rede de Indicados"}
          </h2>
          <ResponsiveContainer width="100%" height={400}>
            <LineChart data={dataFiltrada}>
              <CartesianGrid strokeDasharray="3 3" />
              <XAxis dataKey="mes" stroke="#ccc" />
              <YAxis stroke="#ccc" />
              <Tooltip />
              <Legend />
              {verFinanceiro ? (
                <>
                  <Line type="monotone" dataKey="capital" stroke="#00bcd4" name="Capital Acumulado" />
                  <Line type="monotone" dataKey="lucro" stroke="#4caf50" name="Lucro Mensal" />
                  <Line type="monotone" dataKey="receitaLicenca" stroke="#ff9800" name="Receita com Licenças" />
                  <Line type="monotone" dataKey="comissao" stroke="#f44336" name="Comissões da Rede" />
                </>
              ) : (
                <>
                  <Line type="monotone" dataKey="capitalIndicadoN1" stroke="#9575cd" name="Capital Nível 1" />
                  <Line type="monotone" dataKey="capitalIndicadoN2" stroke="#4dd0e1" name="Capital Nível 2" />
                  <Line type="monotone" dataKey="capitalIndicadoN3" stroke="#f06292" name="Capital Nível 3" />
                </>
              )}
            </LineChart>
          </ResponsiveContainer>
        </CardContent>
      </Card>

      <div className="flex gap-4 mt-4">
        <button
          onClick={() => downloadCSV(data, "visao-financeira")}
          className="bg-cyan-600 hover:bg-cyan-700 text-white text-sm font-semibold py-2 px-4 rounded"
        >
          Exportar Visão Geral (CSV)
        </button>
        <button
          onClick={() =>
            downloadCSV(
              data.map(
                ({ mes, capitalIndicadoN1, capitalIndicadoN2, capitalIndicadoN3 }) => ({
                  mes,
                  capitalIndicadoN1,
                  capitalIndicadoN2,
                  capitalIndicadoN3,
                })
              ),
              "rede-indicados"
            )
          }
          className="bg-purple-600 hover:bg-purple-700 text-white text-sm font-semibold py-2 px-4 rounded"
        >
          Exportar Rede de Indicados (CSV)
        </button>
      </div>
    </div>
  );
}
